import { Container } from "@mui/material";
import Box from "@mui/material/Box";
import Tab from "@mui/material/Tab";
import Tabs from "@mui/material/Tabs";
import axios from "lib/http";
import PropTypes from "prop-types";
import * as React from "react";
import { useState } from "react";
import LoadingBar from "./LoadingBar";
import StateCitySelect from "./StateCitySelect";
import states from "./state_city_data.json";
import "react-notifications/lib/notifications.css";
import {
  NotificationContainer,
  NotificationManager,
} from "react-notifications";
const SERVER_URL = "https://node-scrap.vercel.app";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrape-tabpanel-${index}`}
      aria-labelledby={`scrape-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
    </div>
  );
}

function a11yProps(index) {
  return {
    id: `scrape-tab-${index}`,
    "aria-controls": `scrape-tabpanel-${index}`,
  };
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

export default function ScrapeComponent() {
  const [loading, setLoading] = useState(false);
  const [value, setValue] = React.useState(0);
  const [state, setState] = useState(
    states.find((item) => item.stateName === "Ontario") ?? null
  );
  const [city, setCity] = useState({
    cityName: "Toronto",
    cityLink: "/place/toronto-on",
  });

  const handleChange = (event) => {
    setLoading(false);
    const { value = "", name = "" } = event.target;
    if (name === "city") setCity(value);
    else {
      setState(value);
      setCity(null);
    }
  };

  const handleStartScarapping = () => {
    let stateName = "";
    let cityName = "";
    if (value === 1) {
      stateName = state?.stateName ?? "";
      cityName = city?.cityName ?? "";
    }
    setLoading(true);
    axios
      .post(`${SERVER_URL}/scrape`, {
        cityName,
        stateName,
        fullScrape: value === 0 ? true : false,
      })
      .then((res) => {
        if (res.status === 200) {
          const result = res.data?.success ?? false;
          setLoading(false);
          NotificationManager.success(
            "Scraping done successfully.",
            "Scraping"
          );
        }
      })
      .catch((err) => {
        setLoading(false);
        NotificationManager.error("Something went wrong. Try again!");
        console.log(err);
      });
  };

  return (
    <div>
      {loading && <LoadingBar />}
      <Container className="py-5">
        <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
          <Tabs
            value={value}
            onChange={(e, v) => {
              setValue(v);
              setLoading(false);
            }}
          >
            <Tab label="Full Scrape" {...a11yProps(0)} />
            <Tab label="By State / City Scrape" {...a11yProps(1)} />
          </Tabs>
        </Box>
        <TabPanel value={value} index={0}>
          <StateCitySelect
            states={states}
            city={city}
            state={state}
            disabled={true}
            loading={loading}
            onScrape={handleStartScarapping}
          />
        </TabPanel>
        <TabPanel value={value} index={1}>
          <StateCitySelect
            states={states}
            city={city}
            state={state}
            loading={loading}
            onScrape={handleStartScarapping}
            onChange={handleChange}
          />
        </TabPanel>
      </Container>
      <NotificationContainer />
    </div>
  );
}
