import { BrowserRouter, Route, Routes } from "react-router-dom";
import Layout from "views/layout";
import ScrapeComponent from "views/pages/scrape/ScrapeComponent";

export default function Root() {
  return (
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<Layout />}>
          <Route index element={<ScrapeComponent />} />
        </Route>
      </Routes>
    </BrowserRouter>
  );
}
