import { Autocomplete, Button, Grid, TextField } from "@mui/material";

export default function StateCitySelect({
  states = [],
  state = {},
  city = {},
  disabled = false,
  loading = false,
  onChange = () => {},
  onScrape = () => {},
}) {
  return (
    <Grid
      container
      spacing={2}
      rowSpacing={2}
      alignItems="center"
      justifyContent="center"
    >
      <Grid item xs={12} sm={12} md={5}>
        <Autocomplete
          id="state"
          name="state"
          disablePortal
          options={states}
          sx={{ width: 300 }}
          getOptionLabel={(option) => option.stateName || ""}
          size="small"
          value={disabled ? null : state}
          renderInput={(params) => <TextField {...params} label="State" />}
          onChange={(e, v) => onChange({ target: { name: "state", value: v } })}
          disabled={disabled}
          disableClearable
        />
      </Grid>
      <Grid item xs={12} sm={12} md={5}>
        <Autocomplete
          id="city"
          name="city"
          disablePortal
          options={state?.cities ?? []}
          sx={{ width: 300 }}
          getOptionLabel={(option) => option.cityName || ""}
          size="small"
          value={disabled ? null : city}
          renderInput={(params) => <TextField {...params} label="City" />}
          onChange={(e, v) => onChange({ target: { name: "city", value: v } })}
          disabled={disabled}
          disableClearable
        />
      </Grid>
      <Grid item xs={12} sm={12} md={2}>
        <Button
          variant="contained"
          color="primary"
          onClick={onScrape}
          size="large"
        >
          Scrape
        </Button>
      </Grid>
    </Grid>
  );
}
